import * as React from 'react';
import MoveToInboxIcon from '@mui/icons-material/MoveToInbox';
import OutboxIcon from '@mui/icons-material/Outbox';
import ShoppingBasketIcon from '@mui/icons-material/ShoppingBasket';
import AssignmentIcon from '@mui/icons-material/Assignment';
import { NotificationsNone } from '@material-ui/icons';
import { Badge } from '@mui/material';
import { Avatar } from '@material-ui/core';
import { Navbar, Container, NavDropdown, Nav, Form, FormControl, Button } from 'react-bootstrap';

export default function TopBar() {
    return (
    
        <Navbar bg="light" expand="lg">
            <Container fluid>
                <Navbar.Brand href="#" style={{ color: "rgb(2, 2, 92)" }}>Admin Dashboard</Navbar.Brand>
                <Navbar.Toggle aria-controls="navbarScroll" style={{ right: "0", float: "left" }} />
                <Navbar.Collapse id="navbarScroll">
                    <Nav
                        className="me-auto my-2 my-lg-0"
                        style={{ maxHeight: '100px' }}
                        navbarScroll
                    >
                        <Nav.Link href="#action1"><MoveToInboxIcon />Sales Order</Nav.Link>
                        <Nav.Link href="#action2"><OutboxIcon />Purchase orders</Nav.Link>
                        <Nav.Link href="#action2"><ShoppingBasketIcon />Products</Nav.Link>
                        <Nav.Link href="#action2"><AssignmentIcon />Task List</Nav.Link>
                    </Nav>
                    <Form className="d-flex">
                        <FormControl
                            type="search"
                            placeholder="Search"
                            className="me-2"
                            aria-label="Search"
                        />
                        <Button variant="outline-success">Search</Button>
                    </Form>
                    <div className="nottop" style={{ marginTop: "20px" }}>
                    </div>
                    <Badge className="badge" color="secondary" badgeContent={99}>
                        <NotificationsNone color="secondary" />
                    </Badge>
                    <Avatar className="avatar" alt="Remy Sharp" src="data:image/jpeg;base64," />
                    <span style={{padding:"10px"}}>Unza Sohail </span>
                </Navbar.Collapse>

            </Container>
        </Navbar>
           
    );
}